<template>
  <div class="schoolInformation" v-loading.body="bodyLoading">
    <div class="mainContainer">
      <div>
        <crumb style="padding: 0 5px;">
          <div class="new--btn" slot="right" @click="handleAdd" v-show="addShow">+ 新建</div>
        </crumb>
      </div>
      <div style="background: #fff; border-radius: 10px;min-height: 72vh;">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column
           v-for="(item, index) in tableHead"
           :key="index"
           :prop="item.prop"
           :label="item.label"
           :width="item.width">
          </el-table-column>
          <el-table-column label="操作" width="140">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button type="text" style="color: #ff5858" size="small" @click="handleDelete(scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog
      class="myDialog"
      :title="title"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      width="60%">
      <el-form ref="form" :model="form" label-width="120px" :rules="rules">
        <el-row class="logoRow">
          <el-col :span="12" style="width:482px">
            <el-form-item label="学校校徽：" prop="logo">
              <el-upload
                ref="uploadLogo"
                class="avatar-uploader"
                accept=".jpg,.png"
                :action="action"
                :show-file-list="false"
                :headers="headers"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col :span="5" style="letter-spacing: 0.1em;color: #03B187;line-height: 40px;cursor: pointer;">
            <div style="margin-left: 20px" @click="showLogo">查看校徽示例</div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="学校代码：" prop="code">
              <el-input size="small" v-model="form.code" placeholder="请输入学校代码"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="学校名称：" prop="name">
              <el-input size="small" v-model="form.name" placeholder="请输入学校名称"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="所在地区：" prop="province">
              <el-input size="small" v-model="form.province" placeholder="请输入所在地区"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="所在城市：" prop="city">
              <el-input size="small" v-model="form.city" placeholder="请输入所在城市"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="建校日期：" prop="building">
              <el-date-picker
                size="small"
                style="width: 100%"
                v-model="form.building"
                type="month"
                placeholder="请选择建校日期"
                format="yyyy-MM"
                value-format="yyyy-MM">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="学校性质类别：" prop="nature">
              <el-select style="width: 100%" size="small" v-model="form.nature" placeholder="请输入学校性质类别">
                <el-option
                  v-for="item in typeOptions"
                  :key="item.code"
                  :label="item.value"
                  :value="item.code">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="校训：" prop="intro">
              <el-input size="small" v-model="form.intro" placeholder="请输入校训"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="通讯地址：" prop="address">
              <el-input size="small" v-model="form.address" placeholder="请输入通讯地址"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="邮政编码：" prop="postcode">
              <el-input size="small" v-model="form.postcode" placeholder="请输入邮政编码"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="学校网址：" prop="internetAddress">
              <el-input size="small" v-model="form.internetAddress" placeholder="请输入学校网址"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row class="bgRow">
          <el-col :span="12" style="width:482px">
            <el-form-item label="登录页背景图：" prop="indexBackdrop">
              <el-upload
                ref="uploadBg"
                class="avatar-uploader"
                accept=".jpg,.png"
                :action="action1"
                :show-file-list="false"
                :headers="headers"
                :on-success="handleAvatarSuccess1"
                :before-upload="beforeAvatarUpload1">
                <img v-if="imageUrl1" :src="imageUrl1" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col :span="5" style="letter-spacing: 0.1em;color: #03B187;line-height: 40px;cursor: pointer;">
            <div style="margin-left: 20px" @click="showbg">查看背景图示例</div>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogVisible = false" size="small">取 消</el-button>
        <el-button type="primary" v-if="title==='添加学校信息'" @click="submitAdd" size="small" :loading="loading">确 定</el-button>
        <el-button type="primary" v-if="title==='编辑学校信息'" @click="submitEdit" size="small" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
    <div class="showImg" v-show="showImg" @click="maskClick">
      <img :src="exampleImg" alt="">
    </div>
  </div>
</template>
<script>
export default {
  name: "SchoolInformation",
  data() {
    return {
      bodyLoading: true,
      tableHead: [
        {
          label: '学校代码',
          prop: 'code',
          // width: 120
        },
        {
          label: '学校名称',
          prop: 'name',
          // width: 120
        },
        {
          label: '所在地区',
          prop: 'province',
          // width: 120
        },
        {
          label: '所在城市',
          prop: 'city',
          // width: 120
        },
        {
          label: '建校日期',
          prop: 'building',
          // width: 130
        },
        {
          label: '学校性质类别',
          prop: 'natureName',
          // width: 180
        },
        {
          label: '校训',
          prop: 'intro',
          // width: 150
        },
        {
          label: '通信地址',
          prop: 'address',
          // width: 220
        },
        {
          label: '邮政编码',
          prop: 'postcode',
          // width: 120
        },
        {
          label: '学校网址',
          prop: 'internetAddress',
          // width: 150
        }
      ],
      tableData: [],
      dialogVisible: false,
      title: '添加学校信息',
      form: {},
      rules: {
        code: [
          { required: true, message:'请输入学校代码', trigger: 'blur' }
        ],
        name: [
          { required: true, message:'请输入学校名称', trigger: 'blur' }
        ]
      },
      action: '',
      action1: '',
      imageUrl: '',
      imageUrl1: '',
      showImg: false,
      exampleImg: '',
      form: {
        code: '',
        name: '',
        province: '',
        city: '',
        building: '',
        nature: 0,
        intro: '',
        address: '',
        postcode: '',
        internetAddress: ''
      },
      typeOptions: [],
      countDown: '',
      headers: {},
      addShow: false,
      loading: false
    }
  },
  mounted() {
    this.getList()
    this.$get('/system/dict/field/nature').then(res => {
      this.typeOptions = res.data
    })
  },
  methods: {
    getList() {
      this.bodyLoading = true
      this.$post('/basic/shcool/list', {page: 1, rows: 1}).then(res => {
        if(res.status === 1) {
          this.tableData = res.data.list
          res.data.list.length === 0 ? this.addShow = true : this.addShow = false
        } else {
          this.$notify.error({
            title: '提示',
            message: res.message,
            duration: 2000
          })
        }
        this.bodyLoading = false
      })
    },
    getObj(obj) {
      return JSON.parse(JSON.stringify(obj))
    },
    resetForm() {
      this.form = {
        code: '',
        name: '',
        province: '',
        city: '',
        building: '',
        nature: 0,
        intro: '',
        address: '',
        postcode: '',
        internetAddress: ''
      }
    },
    handleAdd() {
      this.dialogVisible = true
      this.title= '添加学校信息'
      let token = localStorage.getItem('x_token')
      this.headers = {
        'X-Token': token
      }
      this.action = this.API_URL + '/system/file/editorUpload'
      this.action1 = this.API_URL + '/system/file/editorUpload'
      this.resetForm()
      this.$nextTick(() => {
        this.$refs.form.resetFields()
        this.$refs.uploadLogo.clearFiles()
        this.$refs.uploadBg.clearFiles()
        this.imageUrl = ''
        this.imageUrl1 = ''
      })
    },
    handleEdit(row) {
      this.dialogVisible = true
      this.title= '编辑学校信息'
      let token = localStorage.getItem('x_token')
      this.headers = {
        'X-Token': token
      }
      this.action = this.API_URL + '/system/file/editorUpload'
      this.action1 = this.API_URL + '/system/file/editorUpload'
      this.$nextTick(() => {
        this.$refs.form.resetFields()
        this.form = this.getObj(row)
        this.form.logo ? this.imageUrl = this.form.logo : this.imageUrl = ''
        this.form.indexBackdrop ? this.imageUrl1 = this.form.indexBackdrop : this.imageUrl1 = ''
        this.form.nature = Number(this.form.nature)
      })
    },
    submitAdd() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true
          this.$post('/basic/shcool/add', this.form).then(res => {
            if(res.status === 1) {
              this.$notify.success({
                title: '提示',
                message: res.message,
                duration: 2000
              })
              this.getList()
              this.dialogVisible = false
            } else {
              this.$notify.error({
                title: '提示',
                message: res.message,
                duration: 2000
              })
            }
            this.loading = false
          })
        }
      })
    },
    submitEdit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.loading = true
          this.$put('/basic/shcool/edit', this.form).then(res => {
            if(res.status === 1) {
              this.$notify.success({
                title: '提示',
                message: res.message,
                duration: 2000
              })
              this.getList()
              this.dialogVisible = false
            } else {
              this.$notify.error({
                title: '提示',
                message: res.message,
                duration: 2000
              })
            }
            this.loading = false
          })
        }
      })
    },
    handleAvatarSuccess(res, file) {
      if(res.success===0) {
        this.$notify.success({
          title: '提示',
          message: '上传成功!',
          duration: 2000
        })
        this.imageUrl = res.url
        this.form.logo = res.url
      } else {
        this.$notify.error({
          title: '提示',
          message: '上传失败!',
          duration: 2000
        })
      }
    },
    beforeAvatarUpload(file) {
      const isLt20M = file.size / 1024 / 1024 < 20
      if (!isLt20M) {
        this.$notify.error({
          title: '提示',
          message: '上传图片大小不能超过20MB!',
          duration: 2000
        })
      }
      return isLt20M
    },
    handleAvatarSuccess1(res, file) {
      if(res.success===0) {
        this.$notify.success({
          title: '提示',
          message: '上传成功!',
          duration: 2000
        })
        this.imageUrl1 = res.url
        this.form.indexBackdrop = res.url
      } else {
        this.$notify.error({
          title: '提示',
          message: '上传失败!',
          duration: 2000
        })
      }
    },
    beforeAvatarUpload1(file) {
      const isLt20M = file.size / 1024 / 1024 < 20
      if (!isLt20M) {
        this.$notify.error({
          title: '提示',
          message: '上传图片大小不能超过20MB!',
          duration: 2000
        })
      }
      return isLt20M
    },
    showLogo() {
      this.exampleImg = require('@/assets/img/xiao.png')
      this.showImg = true
    },
    showbg() {
      this.exampleImg = require('@/assets/img/bei.png')
      this.showImg = true
    },
    maskClick() {
      this.showImg = false
    },
    handleDelete(row) {
      this.$confirm('此操作将删除学校信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$deletes(`/basic/shcool/del/${row.id}`).then(res => {
          if(res.status === 1) {
            this.$notify.success({
              title: '提示',
              message: res.message,
              duration: 2000
            })
            this.getList()
          } else {
            this.$notify.error({
              title: '提示',
              message: res.message,
              duration: 2000
            })
          }
        })
      }).catch(() => {
        this.$notify.error({
          title: '提示',
          message: '已取消删除!',
          duration: 2000
        })
      })
    }
  }
};
</script>
<style lang="scss">
.schoolInformation {
  .el-dialog {
    margin-top: 2vh!important;
  }
  .myDialog{
    .el-col {
      padding-right: 30px;
    }
    .logoRow {
      .avatar-uploader .el-upload {
        background: #f4f5f6;
        border: 1px solid #eee;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }
      .avatar-uploader .el-upload:hover {
        border-color: #03B187;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width:362px;
        height:72px;
        line-height: 72px;
        text-align: center;
        &::before {
          content: "\e79f";
          color: #03B187;
        }
        &::after {
          display: block;
          content: "点击此处上传";
          color: #03B187;
          font-size:14px;
          color: #323232;
          height: 15px;
          line-height: 15px;
          letter-spacing: 0.1em;
        }
      }
      .avatar {
        width:362px;
        // height:102px;
        display: block;
      }
    }
    .bgRow {
      .avatar-uploader .el-upload {
        background: #f4f5f6;
        border: 1px solid #eee;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }
      .avatar-uploader .el-upload:hover {
        border-color: #03B187;
      }
      .avatar-uploader-icon {
        font-size: 50px;
        color: #8c939d;
        width: 362px;
        height: 202px;
        line-height: 135px;
        text-align: center;
        &::before {
          content: "\e79f";
          color: #03B187;
        }
        &::after {
          display: block;
          content: "点击此处上传";
          color: #03B187;
          font-size:14px;
          color: #323232;
          height: 15px;
          line-height: 15px;
          letter-spacing: 0.1em;
        }
      }
      .avatar {
        width:362px;
        // height:102px;
        display: block;
      }
    }
  }
  .showImg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 99999;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%)
    }
  }
}
</style>


